body {
  background: url(/images/bg.jpg);
  background-size: cover;
  background-position: center;
}
.loginRoot {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  background-blend-mode: lighten;
  background: rgba(77, 77, 77, 0.521);
  font-family: sans-serif;
}


.loginBox {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 350px;
  height: 600px;
  padding: 80px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-image: url(/images/jetcab-bg.png);  
  /* background: rgba(0, 0, 0, 0.5); */
  background-size: cover;
  background-position: center;
}

.user{
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  position: absolute;
  top: calc(-100px/2);
  left: calc(50% - 50px);
}

h2 {
  margin: 0;
  padding: 0 0 30px;
  color: #efed40;
  text-align: center;
}

.loginBox .p-white{
  margin: 0;
  padding: 0;
  font-weight: bold;
  color: #fff;
}
.loginBox input {
  width: 100%;
  margin-bottom: 20px;
}
.loginBox input[type="email"],
.loginBox input[type="password"]{
  border: none;
  border-bottom: 1px solid #fff;
  background: transparent;
  color: #fff;
  outline: none;
  height: 40px;
  font-size: 1rem;
}
::-webkit-input-placeholder {
  color: rgba(255,255,255, 0.5);
}
:-ms-input-placeholder {
  color: rgba(255,255,255, 0.5);
}
::placeholder {
  color: rgba(255,255,255, 0.5);
}
.loginBox input[type="submit"] {
  border: none;
  outline: none;
  color: #fff;
  font-size: 16px;
  height: 40px;
  background: #ff267e;
  border-radius: 20px;
  cursor: pointer;
}
.loginBox input[type="submit"]:hover {
  background: #efed40;
  color: #262626;
}

.loginBox a {
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
}


.global-error {
  text-color: black;
  padding: 10px;
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 20px 0px black;
          box-shadow: inset 0 0 20px 0px black;
}



